import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreDocument, AngularFirestoreCollection} from '@angular/fire/firestore';
import { AngularFireStorage } from '@angular/fire/storage';
import { finalize } from 'rxjs/operators';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

type CollectionPredicate<T> = string | AngularFirestoreCollection<T>;
type DocPredicate<T> = string | AngularFirestoreDocument<T>;

@Injectable({providedIn: 'root'})
export class FirestoreService {

  constructor(private afs: AngularFirestore, private storage: AngularFireStorage) { }

  col<T>(ref: CollectionPredicate<T>, queryFn?: any): AngularFirestoreCollection<T> {
    return typeof ref === 'string' ? this.afs.collection<T>(ref, queryFn) : ref;
  }

  doc<T>(ref: DocPredicate<T>): AngularFirestoreDocument<T> {
    return typeof ref === 'string' ? this.afs.doc<T>(ref) : ref;
  }

  col$<T>(ref: CollectionPredicate<T>, queryFn?: any): Observable<T[]> {
    return this.col(ref, queryFn).snapshotChanges().pipe(map( 
      docs => docs.map(a => ({ uid: a.payload.doc.id, ...a.payload.doc.data() }))));
  }

  doc$<T>(ref: DocPredicate<T>): Observable<any> {
      return this.doc(ref).snapshotChanges().pipe(map(
        doc => ({uid: doc.payload.id, ...doc.payload.data()})));
  }

  //-----------down not tested---------------------
  add$<T>(obj: T, ref: CollectionPredicate<T>): Promise<any> {
    // console.log(obj);
    // console.log(ref);
    // let nobj: any = obj;
    // nobj.uid = null
    return this.col(ref).add({...obj});
  }

  create$<T>(uid:any, obj: T, ref: CollectionPredicate<T>): Promise<any> {
    return this.col(ref).doc(uid).set({...obj});
  }

  update$<T>(obj: T, ref: DocPredicate<T>): Promise<void> {
    return this.doc(ref).update({...obj});
  }

  updateByUid$<T>(uid: any, obj: T, ref: CollectionPredicate<T>): Promise<void> {
    return this.col(ref).doc(uid).update({...obj});
  }
 
  delete$<T>(ref: DocPredicate<T>): Promise<void> {
    return this.doc(ref).delete();
  }

  deleteAll$<T>(ref: CollectionPredicate<T>) {
    return this.col(ref).get().subscribe(
      qs => {
        qs.forEach( doc => doc.ref.delete)
      }, err => {
        console.error(err);
      });
  }


  //-------------File Upload-----------------------
  uploadFile(file: File, path: string): Observable<number> {
    const filePath = `${path}/${file.name}`;
    const storageRef = this.storage.ref(filePath);
    const uploadTask = this.storage.upload(filePath, file);
    uploadTask.snapshotChanges().pipe(
      finalize(() => {
        storageRef.getDownloadURL().subscribe(downloadURL => {
          
        });
      })
    ).subscribe();
    return uploadTask.percentageChanges();
  }

  deleteFileStorage(name: string, path: string): void {
    this.storage.ref(path).child(name).delete();
  }

  
  // col$<T>(ref: CollectionPredicate<T>, queryFn?): Observable<T[]> {
  //   return this.col(ref, queryFn).snapshotChanges().pipe(map(docs => docs.map(a => a.payload.doc.data()) as T[]));
  // }

  // doc$<T>(ref: DocPredicate<T>): Observable<T> {
  //   return this.doc(ref).snapshotChanges().pipe(map(doc => doc.payload.data() as T));
  // }

}
