import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from '../security/gaurds/auth.guard';
import { IconsFontAwesomeComponent } from '../shared/icons-font-awesome/icons-font-awesome.component';
import { IconsLinearComponent } from '../shared/icons-linear/icons-linear.component';
import { ProfileDetailsComponent } from './profile-details/profile-details.component';
import { AddProjectDetailsComponent } from './project-list/add-project-details/add-project-details.component';
import { ProjectListComponent } from './project-list/project-list.component';
import { UsedToolsComponent } from './used-tools/used-tools.component';
import { VisitorMessageComponent } from './visitor-message/visitor-message.component';


const routes: Routes = [
  { path: 'mod-faw-icon', component: IconsFontAwesomeComponent, canActivate: [AuthGuard] },
  { path: 'mod-lin-icon', component: IconsLinearComponent, canActivate: [AuthGuard] },

  {
    component: ProfileDetailsComponent,
    canActivate: [AuthGuard],
    path: '',
  },
  {
    component: ProfileDetailsComponent,
    canActivate: [AuthGuard],
    path: 'profile-details',
  },
  {
    component: ProjectListComponent,
    canActivate: [AuthGuard],
    path: 'project-list',
  },
  {
    component: AddProjectDetailsComponent,
    canActivate: [AuthGuard],
    path: 'add-project',
  },
  {
    component: AddProjectDetailsComponent,
    canActivate: [AuthGuard],
    path: 'add-project/:id',
  },
  {
    component: UsedToolsComponent,
    canActivate: [AuthGuard],
    path: 'used-tools',
  },
  {
    component: VisitorMessageComponent,
    canActivate: [AuthGuard],
    path: 'visitor-message',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminLayoutRoutingModule { }
