import { Component, OnInit } from '@angular/core';
import { VisitorMessage } from '../../shared/_models/visitor-message.model';
import { FirestoreService } from '../../shared/_services/firestore.service';
import { BasePaths } from '../../shared/_utils/base-paths.enum';

@Component({
  selector: 'app-visitor-message',
  templateUrl: './visitor-message.component.html',
  styleUrls: ['./visitor-message.component.css']
})
export class VisitorMessageComponent implements OnInit {

  visitorMessageList: VisitorMessage[] = [];

  constructor(
    private dbService: FirestoreService,
  ) { }

  ngOnInit(): void {
    this.getVisitorMessageList();
  }

  getVisitorMessageList() {
    this.dbService.col$<VisitorMessage>(BasePaths.visitor_message).subscribe(messages => {
      this.visitorMessageList = messages;
    });
  }

  deleteMessage(message: VisitorMessage) {
    this.dbService.delete$<VisitorMessage>(BasePaths.visitor_message + '/' + message.uid).then(
      res => { console.log('res', res) },
      err => { console.log('err', err) }
    );
  }

}
