import { Component, OnInit } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { AngularFireStorage } from '@angular/fire/storage';
import { FirestoreService } from 'src/app/layouts/shared/_services/firestore.service';
import { BasePaths } from '../../shared/_utils/base-paths.enum';
import { ProfileDetails } from '../../shared/_models/profile-details.model';
import { ToastrService } from 'ngx-toastr';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmObj } from '../../shared/_models/confirm.model';
import { ConfirmModal } from '../../shared/modals/confirm/confirm.modal';
import { ModalSetup } from '../../shared/_models/modal-setup.model';

@Component({
  selector: 'app-profile-details',
  templateUrl: './profile-details.component.html',
  styleUrls: ['./profile-details.component.css']
})
export class ProfileDetailsComponent implements OnInit {

  btnDisProfileDtlSave: boolean = false;
  profileUid: string = 'profile-details-uid';

  profile: any = new ProfileDetails();

  profileImgUrl: any;
  profileImg: File;

  constructor(
    private dialog: MatDialog,
    private dbService: FirestoreService,
    private storage: AngularFireStorage,
    private toastrService: ToastrService,
  ) {
    
  }

  ngOnInit(): void {
    this.getProfileDetails();
  }

  getProfileDetails() {
    this.dbService.doc$<ProfileDetails>(BasePaths.profile_details+'/'+this.profileUid).subscribe(
      res => {
        this.profile = res ? res : new ProfileDetails();
        this.profileImgUrl = this.profile.disImgDownloadLink;
      }, err => {
        console.log('ERROR', err);
      }
    );
  }

  deleteProfileDetails() {
    let confirmObj = new ConfirmObj(1, 'Delete!', 'Do you want to Delete Profile Details?', 'Yes');
    const dialogRef = this.dialog.open(ConfirmModal, new ModalSetup(1, confirmObj).setup);
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dbService.delete$<ProfileDetails>(BasePaths.profile_details + '/' + this.profileUid).then(
          res => { 
            this.toastrService.success('Profile Details Deleted'); 
            this.profile.uid=undefined;
          },
          err => { console.log('err', err) }
        );
      }
    });
  }


  onSubmit() {
    this.btnDisProfileDtlSave = true;

    if (this.profileImg) {
      const imgPath = this.profileImg.name;
      this.profile.disImgPath = imgPath;

      const filePath = `${BasePaths.profile_details}/${imgPath}`;
      const storageRef = this.storage.ref(filePath);
      const uploadTask = this.storage.upload(filePath, this.profileImg);
      uploadTask.snapshotChanges().pipe(
        finalize(() => {
          storageRef.getDownloadURL().subscribe(downloadURL => {
            this.profile.disImgDownloadLink = downloadURL;

            if (this.profile.uid) {
              this.updateProfileDetails();
            } else {
              this.addProfileDetails();
            }

          });
        })
      ).subscribe();
    } else {
      if (this.profile.uid) {
        this.updateProfileDetails();
      } else {
        this.addProfileDetails();
      }
    }
  }

  addProfileDetails() {
    this.dbService.create$<ProfileDetails>(this.profileUid, this.profile, BasePaths.profile_details).then(
      res => {
        this.toastrService.success('Profile Details Saved.');
        this.btnDisProfileDtlSave = false;
      },
      err => {
        this.btnDisProfileDtlSave = false;
      }
    )
  }
  updateProfileDetails() {
    this.dbService.updateByUid$<ProfileDetails>(this.profile.uid, this.profile, BasePaths.profile_details).then(
      res => {
        this.toastrService.success('Profile Details Updated.');
        this.btnDisProfileDtlSave = false;
      },
      err => {
        this.btnDisProfileDtlSave = false;
      }
    )
  }

  selectFile(event) {
    if (event.target.files.length == 0) {
      return;
    }
    this.profileImg = event.target.files[0];
    if (this.profileImg.type.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.profileImg);
    reader.onload = (_event) => {
      this.profileImgUrl = reader.result;
    }
  }

}
