import { Component, OnInit, HostBinding, Input } from '@angular/core';

import { Router } from '@angular/router';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { NavItem } from '../../_models/nav-item';

@Component({
  selector: 'app-menu-nav-item-href',
  templateUrl: './menu-nav-item-href.component.html',
  styleUrls: ['./menu-nav-item-href.component.css'],
  animations: [
    trigger('indicatorRotate', [
      state('collapsed', style({transform: 'rotate(0deg)'})),
      state('expanded', style({transform: 'rotate(-90deg)'})),
      transition('expanded <=> collapsed',
        animate('225ms cubic-bezier(0.4,0.0,0.2,1)')
      ),
    ])
  ]
})
export class MenuNavItemHrefComponent implements OnInit {

  expanded: boolean = false;
  @HostBinding('attr.aria-expanded') ariaExpanded = this.expanded;
  @Input() item: NavItem = new NavItem();
  @Input() depth: number = 0;

  constructor(
    public router: Router
  ) {
  }

  ngOnInit() {
  }

  onItemSelected(item: NavItem) {
    if (!item.child || !item.child.length) {
      this.router.navigate([item.route]);
    }
    if (item.child && item.child.length) {
      this.expanded = !this.expanded;
    }
  }

}
