import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProjectDetails } from '../../shared/_models/project-details.model';
import { FirestoreService } from '../../shared/_services/firestore.service';
import { BasePaths } from '../../shared/_utils/base-paths.enum';

@Component({
  selector: 'app-project-list',
  templateUrl: './project-list.component.html',
  styleUrls: ['./project-list.component.css']
})
export class ProjectListComponent implements OnInit {

  projectList: ProjectDetails[] = [];

  constructor(
    public router: Router,
    private dbService: FirestoreService,
  ) { }

  ngOnInit(): void {
    this.getProjectList();
  }

  getProjectList() {
    this.dbService.col$<ProjectDetails>(BasePaths.project_details).subscribe(projects => {
      this.projectList = projects;
      // console.log(this.projectList);
    });
  }

  deleteProject(project: ProjectDetails) {
    this.dbService.delete$<ProjectDetails>(BasePaths.project_details + '/' + project.uid).then(
      res => { console.log('res', res) },
      err => { console.log('err', err) }
    );
  }

  editProject(proKey) {
    this.router.navigate(['/admin/add-project/'+proKey]);
  }

}
