export class ModalSetup {

    setup: any = { width: '500px', position: { top: '25px' } };

    //width= 400px, 500px, 720px, 1000px, 1200px;

    constructor(size: number, data: any, enableClose?: boolean, top?: number) {
        this.setup.data = data;
        this.setup.width = this.findSize(size);
        this.setup.position.top = this.findTop(top);
        this.setup.hasBackdrop = true;
        this.setup.disableClose = enableClose?false:true;
    }

    findSize(size: number): string {
        let width = '';
        switch (size) {
            case 1:
                width = '400px';
                break;
            case 2:
                width = '500px';
                break;
            case 3:
                width = '720px';
                break;
            case 4:
                width = '1000px';
                break;
            case 5:
                width = '1200px';
                break;
            default:
                width = '500px';
                break;
        }
        return width;
    }

    findTop(top: number): string {
        if (top) {
            return top + 'px';
        } else {
            return '80px';
        }
    }
}
