import { NgModule } from '@angular/core';
import { CommonModule, HashLocationStrategy, LocationStrategy } from '@angular/common';

import { AdminLayoutRoutingModule } from './admin-layout-routing.module';
import { SecurityModule } from '../security/security.module';
import { SharedModule } from '../shared/shared.module';
import { UsedToolsComponent } from './used-tools/used-tools.component';
import { ProjectListComponent } from './project-list/project-list.component';
import { VisitorMessageComponent } from './visitor-message/visitor-message.component';
import { ProfileDetailsComponent } from './profile-details/profile-details.component';
import { MAT_DIALOG_DEFAULT_OPTIONS } from '@angular/material/dialog';
import { AdminLayoutComponent } from './admin-layout.component';
import { AddProfileDetailsComponent } from './profile-details/add-profile-details/add-profile-details.component';
import { AddProjectDetailsComponent } from './project-list/add-project-details/add-project-details.component';
import { AddProjectBtnComponent } from './project-list/add-project-btn/add-project-btn.component';
import { AddProjectTechnologyComponent } from './project-list/add-project-technology/add-project-technology.component';


@NgModule({
  declarations: [
    AdminLayoutComponent,
    ProfileDetailsComponent,
    ProjectListComponent,
    UsedToolsComponent,
    VisitorMessageComponent,
    AddProfileDetailsComponent,
    AddProjectDetailsComponent,
    AddProjectBtnComponent,
    AddProjectTechnologyComponent,
  ],
  entryComponents: [
    AddProjectBtnComponent,
  ],
  imports: [
    CommonModule,
    SharedModule,
    SecurityModule,
    AdminLayoutRoutingModule,
  ],

  providers: [
    {provide: LocationStrategy, useClass: HashLocationStrategy},
    {provide: MAT_DIALOG_DEFAULT_OPTIONS, useValue: {hasBackdrop: false}}
  ],
})
export class AdminLayoutModule { }
