export class ItemDataModel {

    uid: string;

    sl: number;
    duration: string;
    title: string;
    subTitle: string;
    description: string;
    active: boolean;
    
    imageUrl: string;  //download url
    imagePath?: string; //timestamp_fileName
    imageName?: string; //file original name

    visitLink: string;
    detailsLink: string;

    constructor(sl:number, duration:string, title:string, subTitle:string, description:string, active:boolean, 
        imageUrl?:string, visitLink?:string) {
        this.sl = sl;
        this.duration = duration;
        this.title = title;
        this.subTitle = subTitle;
        this.description = description;
        this.active = active;
        this.imageUrl = imageUrl;
        this.visitLink = visitLink;
        this.detailsLink = this.detailsLink;
    }

}
