import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ModalObj } from '../../shared/_models/modal-obj.model';
import { ModalSetup } from '../../shared/_models/modal-setup.model';
import { ProfileDetails } from '../../shared/_models/profile-details.model';
import { ProjectButton } from '../../shared/_models/project-button.model';
import { ProjectDetails } from '../../shared/_models/project-details.model';
import { ProjectTechnology } from '../../shared/_models/project-technology.model';
import { UsedTools } from '../../shared/_models/used-tools.model';
import { VisitorMessage } from '../../shared/_models/visitor-message.model';
import { FirestoreService } from '../../shared/_services/firestore.service';
import { BasePaths } from '../../shared/_utils/base-paths.enum';
import { ViewProjectDetailsComponent } from '../view-project-details/view-project-details.component';

@Component({
  selector: 'app-fullwidth-home',
  templateUrl: './fullwidth-home.component.html',
  styleUrls: ['./fullwidth-home.component.css']
})
export class FullwidthHomeComponent implements OnInit {

  // toolList: any[] = [{'id':1}, {'id':2}, {'id':3}, {'id':4}, {'id':1}, {'id':2}, {'id':3}, {'id':4}];

  profile: ProfileDetails = new ProfileDetails();
  usedToolList: UsedTools[] = [];
  projectDtlList: ProjectDetails[] = [];

  visitorMessage: VisitorMessage = new VisitorMessage();
  btnDisVisitorMsg: boolean = false;
  visitorMsgThanks: string;

  constructor(
    private dialog: MatDialog,
    private dbService: FirestoreService,
  ) { }

  ngOnInit(): void {
    // console.log(this.toolList);
    this.getProfileDetails();
    this.getProjectDtlList();
    this.getUsedToolsList();
  }

  getProfileDetails() {
    this.dbService.doc$<ProfileDetails>(BasePaths.profile_details+'/'+'profile-details-uid').subscribe(
      res => {
        this.profile = res ? res : new ProfileDetails();
        // this.profileImgUrl = this.profile.disImgDownloadLink;
      }, err => {
        console.log('ERROR', err);
      }
    );
  }

  getProjectDtlList() {
    this.dbService.col$<ProjectDetails>(BasePaths.project_details).subscribe(projects => {
      this.projectDtlList = projects;
      if(this.projectDtlList) {
        this.projectDtlList.forEach((element, idx) => {
          this.dbService.col$<ProjectButton>(BasePaths.project_details + '/' + element.uid + '/actionBtnList').subscribe(
            res => {
              this.projectDtlList[idx].actionBtnList = res;
            }, err => {
              console.log('ERROR', err);
            }
          );
          this.dbService.col$<ProjectTechnology>(BasePaths.project_details + '/' + element.uid + '/projectTechList').subscribe(
            res => {
              this.projectDtlList[idx].usedTechnologyList = res;
            }, err => {
              console.log('ERROR', err);
            }
          );
        });
        console.log('projectDtlList', this.projectDtlList);
      }
    });
  }
  getUsedToolsList() {
    this.dbService.col$<UsedTools>(BasePaths.user_tools).subscribe(tools => {
      this.usedToolList = tools;
    });
  }

  sendVisitorMessage() {
    if(this.isVisitorMassageValid()) {
      this.btnDisVisitorMsg = true;
      this.dbService.add$<VisitorMessage>(this.visitorMessage, BasePaths.visitor_message).then(
        res => {
          this.visitorMessage = new VisitorMessage();
          this.visitorMsgThanks = 'Thanks for Your Message';
        },
        err => {
          this.btnDisVisitorMsg = false;
        }
      )
    }
  }
  isVisitorMassageValid(): boolean {
    if(!this.visitorMessage.visitorName) {
      return false;
    } else if(!this.visitorMessage.visitorEmail) {
      return false;
    } else if(!this.visitorMessage.messageTitle) {
      return false;
    } else if(!this.visitorMessage.messageBody) {
      return false;
    }
    return true;
  }

  actionBtnClick(actionBtn: any, project: any) {
    if (actionBtn.actionType == 2) {
      window.open(actionBtn.actionLink, "_blank");
    } else if (actionBtn.actionType == 1){
      let modalObj = new ModalObj(true, 4, project);
      const dialogRef = this.dialog.open(ViewProjectDetailsComponent, new ModalSetup(4, modalObj, false, 25).setup);
      dialogRef.afterClosed().subscribe(result => {
        if(result) {

        }
      });
    }
  }

}
