import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FullwidthLayoutComponent } from './layouts/fullwidth-layout/fullwidth-layout.component';
import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';
import { AuthGuard } from './layouts/security/gaurds/auth.guard';

const routes: Routes = [
  {
    path: 'admin',
    canActivate: [AuthGuard],
    component: AdminLayoutComponent,
    loadChildren: () => import('./layouts/admin-layout/admin-layout.module').then(m => m.AdminLayoutModule)
  },
  {
    path: '',
    component: FullwidthLayoutComponent,
    loadChildren: () => import('./layouts/fullwidth-layout/fullwidth-layout.module').then(m => m.FullwidthLayoutModule)
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
