import { AppUser } from './app-user.model';

export class ModalObj {

    type: number; //0= title, 1=Add, 2=Edit, 3=Delete, 4=View, 5=Select, 6=New
    data: any;
    success: boolean;
    message: string;
    title: string;
    appUser: AppUser;

    constructor(success: boolean, type: number, data: any, message?: string, title?: string) {

        this.success = success;
        this.type = type;
        this.data = data;

        this.message = message;
        this.title = this.findTitle(type, title);
    }

    findTitle(type: number, title: string): string {
        switch (type) {
            case 1:
                title = 'Add';
                break;
            case 2:
                title = 'Edit';
                break;
            case 3:
                title = 'Delete';
                break;
            case 4:
                title = 'View';
                break;
            case 5:
                title = 'Select';
                break;
            case 6:
                title = 'New';
                break;
            default:
                title = title;
                break;
        }
        return title;
    }
}
