import { Component, OnInit } from '@angular/core';
import { ProjectDetails } from 'src/app/layouts/shared/_models/project-details.model';
import { BasePaths } from 'src/app/layouts/shared/_utils/base-paths.enum';
import { finalize } from 'rxjs/operators';
import { AngularFireStorage } from '@angular/fire/storage';
import { FirestoreService } from 'src/app/layouts/shared/_services/firestore.service';
import { ActivatedRoute } from '@angular/router';
import { ModalObj } from 'src/app/layouts/shared/_models/modal-obj.model';
import { AddProjectBtnComponent } from '../add-project-btn/add-project-btn.component';
import { ModalSetup } from 'src/app/layouts/shared/_models/modal-setup.model';
import { MatDialog } from '@angular/material/dialog';
import { ProjectButton } from 'src/app/layouts/shared/_models/project-button.model';
import { AddProjectTechnologyComponent } from '../add-project-technology/add-project-technology.component';
import { ProjectTechnology } from 'src/app/layouts/shared/_models/project-technology.model';

@Component({
  selector: 'app-add-project-details',
  templateUrl: './add-project-details.component.html',
  styleUrls: ['./add-project-details.component.css']
})
export class AddProjectDetailsComponent implements OnInit {

  project: ProjectDetails = new ProjectDetails();
  usedTechnology: any[] = [];    
  actionBtnList: ProjectButton[] = [];
  usedTechnologyList: ProjectTechnology[] = [];

  projectImg: File;
  projectImgUrl: any;

  btnDisProjectSave: boolean = false;


  constructor(
    private dialog: MatDialog,
    private dbService: FirestoreService,
    private storage: AngularFireStorage,
    private activatedRoute: ActivatedRoute,
  ) {
    
  }

  ngOnInit(): void {
    if(this.activatedRoute.snapshot.params['id']) {
      this.getProjectDetailsByUid(this.activatedRoute.snapshot.params['id']);
    };
  }
  
  getProjectDetailsByUid(uid) {
    this.dbService.doc$<ProjectDetails>(BasePaths.project_details + '/' + uid).subscribe(
      res => {
        this.project = res ? res : new ProjectDetails();
        this.projectImgUrl = this.project.disImgDownloadLink;
        this.getActionButtonList(this.project.uid);
        this.getUsedTechnologyList(this.project.uid);
      }, err => {
        console.log('ERROR', err);
      }
    );
  }
  getActionButtonList(uid: string) {
    this.dbService.col$<ProjectButton>(BasePaths.project_details + '/' + uid + '/actionBtnList').subscribe(
      res => {
        this.actionBtnList = res ? res : [];
      }, err => {
        console.log('ERROR', err);
      }
    );
  }
  getUsedTechnologyList(uid: string) {
    this.dbService.col$<ProjectTechnology>(BasePaths.project_details + '/' + uid + '/projectTechList').subscribe(
      res => {
        this.usedTechnologyList = res ? res : [];
      }, err => {
        console.log('ERROR', err);
      }
    );
  }

  selectFile(event): void {
    if (event.target.files.length == 0) {
      return;
    }
    this.projectImg = event.target.files[0];
    if (this.projectImg.type.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.projectImg);
    reader.onload = (_event) => {
      this.projectImgUrl = reader.result;
    }

    console.log(this.projectImg.name);
  }

  saveProjectDetails() {
    this.btnDisProjectSave = true;

    if (this.projectImg) {
      const imgPath = this.projectImg.name;
      this.project.disImgPath = imgPath;
      

      const filePath = `${BasePaths.project_details}/${imgPath}`;
      const storageRef = this.storage.ref(filePath);
      const uploadTask = this.storage.upload(filePath, this.projectImg);
      uploadTask.snapshotChanges().pipe(
        finalize(() => {
          storageRef.getDownloadURL().subscribe(downloadURL => {

            this.project.disImgDownloadLink = downloadURL;
            console.log(this.project.uid);
            
            if (this.project.uid) {
              this.updateProjectDetails();
            } else {
              this.addProjectDetails();
            }

          });
        })
      ).subscribe();
    } else {
      if (this.project.uid) {
        this.updateProjectDetails();
      } else {
        this.addProjectDetails();
      }
    }

  }

  addProjectDetails() {
    this.dbService.add$<ProjectDetails>(this.project, BasePaths.project_details).then(
      res => {
        this.project = new ProjectDetails();
        this.projectImg = undefined;
        this.projectImgUrl = undefined;
        this.btnDisProjectSave = false;
      },
      err => {
        this.btnDisProjectSave = false;
      }
    )
  }
  updateProjectDetails() {
    this.dbService.updateByUid$<ProjectDetails>(this.project.uid, this.project, BasePaths.project_details).then(
      res => {
        this.project = new ProjectDetails();
        this.projectImg = undefined;
        this.projectImgUrl = undefined;
        this.btnDisProjectSave = false;
      },
      err => {
        this.btnDisProjectSave = false;
      }
    )
  }


  
  actionBtnClick(actionBtn: any) {
    if (actionBtn.actionType == 2) {
      window.open(actionBtn.actionLink, "_blank");
    } else {
      
    }
  }
  
  addProjectActionButton() {
    let btnProject = new ProjectButton();
    btnProject.uidProj = this.project.uid; 
    btnProject.actionType = 2;
    btnProject.btnSl = this.actionBtnList?(this.actionBtnList.length+1):0;
    let modalObj = new ModalObj(true, 1, btnProject);
    const dialogRef = this.dialog.open(AddProjectBtnComponent, new ModalSetup(2, modalObj).setup);
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
      }
    });
  }
  editActionBtn(actionBtn: any) {
    actionBtn.uidProj = this.project.uid;    
    let modalObj = new ModalObj(true, 2, actionBtn);
    const dialogRef = this.dialog.open(AddProjectBtnComponent, new ModalSetup(2, modalObj).setup);
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
      }
    });
  }
  
  //===============================================
  addProjectUsedTechnology() {
    let techProject = new ProjectTechnology();
    techProject.techSl = this.usedTechnologyList?(this.usedTechnologyList.length+1):0;
    techProject.uidProj = this.project.uid;
    let modalObj = new ModalObj(true, 1, techProject);
    const dialogRef = this.dialog.open(AddProjectTechnologyComponent, new ModalSetup(2, modalObj).setup);
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
      }
    });
  }
  editUsedTechnology(usedTech: any) {
    usedTech.uidProj = this.project.uid; 
    let modalObj = new ModalObj(true, 2, usedTech);
    const dialogRef = this.dialog.open(AddProjectTechnologyComponent, new ModalSetup(2, modalObj).setup);
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
      }
    });
  }
  
}
