import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProjectTechnology } from 'src/app/layouts/shared/_models/project-technology.model';
import { ModalObj } from 'src/app/layouts/shared/_models/modal-obj.model';
import { FirestoreService } from 'src/app/layouts/shared/_services/firestore.service';
import { BasePaths } from 'src/app/layouts/shared/_utils/base-paths.enum';

@Component({
  selector: 'app-add-project-technology',
  templateUrl: './add-project-technology.component.html',
  styleUrls: ['./add-project-technology.component.css']
})
export class AddProjectTechnologyComponent implements OnInit {

 
  techProject: ProjectTechnology = new ProjectTechnology();
  btnDisProjBtnSave: boolean = false;

  projectImg: File;
  projectImgUrl: any;

  constructor(
    @Inject(MAT_DIALOG_DATA) public modalObj: ModalObj,
    public dialogRef: MatDialogRef<AddProjectTechnologyComponent>,
    private dbService: FirestoreService,
  ) { }


  ngOnInit(): void {
    if (this.modalObj.data) {
      this.techProject = this.modalObj.data;
    }
    if(this.modalObj.type==1) {
      this.techProject.isActive = 1;
    }
  }

  onSubmit() {
    if (this.techProject.uid) {
      this.updateProjectActionBtn();
    } else {
      this.addProjectActionBtn();
    }
  }

  addProjectActionBtn() {
    this.btnDisProjBtnSave = true;
    this.dbService.add$<ProjectTechnology>(this.techProject, BasePaths.project_details + '/' + this.techProject.uidProj + '/projectTechList').then(
      res => {
        this.btnDisProjBtnSave = false;
        this.modalObj.data = this.techProject;
        this.dialogRef.close(this.modalObj);
      },
      err => {
        this.btnDisProjBtnSave = false;
      }
    )
  }
  updateProjectActionBtn() {

    this.btnDisProjBtnSave = true;
    this.dbService.updateByUid$<ProjectTechnology>(this.techProject.uid, this.techProject, BasePaths.project_details + '/' + this.techProject.uidProj + '/projectTechList').then(
      res => {
        this.btnDisProjBtnSave = false;
        this.modalObj.data = this.techProject;
        this.dialogRef.close(this.modalObj);
      },
      err => {
        this.btnDisProjBtnSave = false;
      }
    )
  }

  //===========================
  selectFile(event): void {
    if (event.target.files.length == 0) {
      return;
    }
    this.projectImg = event.target.files[0];
    if (this.projectImg.type.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.projectImg);
    reader.onload = (_event) => {
      this.projectImgUrl = reader.result;
    }
  }

}
