import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';
import * as moment from 'moment';
import { BasePaths } from '../../shared/_utils/base-paths.enum';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor( ) { }

  userLogin(username: string, password: string): boolean {
    if (username.trim() == BasePaths.username && password.trim() == BasePaths.password) {
      this.addUser({'username': username.trim(), 'password': password.trim()});
      return true;
    } else {
      return false;
    }
  }

  isLoggedIn(): boolean {
    let user = JSON.parse(localStorage.getItem('loginUser'));
    if (user!=null && (user.username.trim() == BasePaths.username && user.password.trim() == BasePaths.password)) {
      return true;
    } else {
      return false;
    }
  }

  getLoginUser(): any {
    return JSON.parse(localStorage.getItem('loginUser'));
  }

  addUser(usr: any) {
    usr.loginMs = moment().valueOf();
    localStorage.setItem('loginUser', JSON.stringify(usr));
  }

  removeUser() {
    localStorage.removeItem('loginUser')
  }

  getAccessToken(): string {
    return this.getLoginUser().accessToken;
  }

  getHeader(): HttpHeaders {
    return new HttpHeaders().set('Authorization', `Bearer ${this.getAccessToken()}`);
  }

}
