import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ModalObj } from '../../shared/_models/modal-obj.model';
import { ProjectDetails } from '../../shared/_models/project-details.model';
import { FirestoreService } from '../../shared/_services/firestore.service';

@Component({
  selector: 'app-view-project-details',
  templateUrl: './view-project-details.component.html',
  styleUrls: ['./view-project-details.component.css']
})
export class ViewProjectDetailsComponent implements OnInit {

  project: ProjectDetails = new ProjectDetails();
  btnDisProjectView: boolean = false;

  constructor(
    @Inject(MAT_DIALOG_DATA) public modalObj: ModalObj,
    public dialogRef: MatDialogRef<ViewProjectDetailsComponent>,
    private dbService: FirestoreService,
  ) { }


  ngOnInit(): void {
    if (this.modalObj.data) {
      this.project = this.modalObj.data;
    }
  }

}
