import { Component, Input, OnInit } from '@angular/core';
import { ProfileDetails } from 'src/app/layouts/shared/_models/profile-details.model';
import { ProfileData } from '../_dataModel/profile-data';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  @Input() pateTitle: String = '';
  @Input() dataList: any[] = [];

  profileData: ProfileData = new ProfileData();
  basicInfo: any = {};
  contactList: any[] = [];


  constructor() { }

  ngOnInit(): void {
    this.basicInfo = this.profileData.BASIC_INFO;
    this.contactList = this.profileData.CONTACT_LIST;
  }

}
