import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminLoginComponent } from './admin-login/admin-login.component';
import { FullwidthHomeComponent } from './fullwidth-home/fullwidth-home.component';
import { VisitorViewComponent } from './visitor-view/visitor-view.component';


const routes: Routes = [
  // { path: '', component: FullwidthHomeComponent },
  { path: '', component: VisitorViewComponent },
  // { path: 'view', component: VisitorViewComponent },
  
  {
    component: AdminLoginComponent,
    path: 'login',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FullwidthLayoutRoutingModule { }
