export class ConfirmObj {
    type: number; //1=Delete,
    title: string;
    subTitle: string;
    confirmBtn: string;
    cancelBtn: string;
    data: any;
    message: string;

    constructor(type?: number, title?: string, subTitle?: string, confirmBtn?: string, cancelBtn?: string) {
        this.type = type?type:1;
        this.title = title?title:'Delete!';
        this.subTitle = subTitle?subTitle:'Are you sure?';
        this.confirmBtn = confirmBtn?confirmBtn:'Yes';
        this.cancelBtn = cancelBtn?cancelBtn:'Cancel';
    }
}
