import { Component, OnInit } from '@angular/core';
import { AngularFireStorage } from '@angular/fire/storage';
import { finalize } from 'rxjs/operators';
import { UsedTools } from '../../shared/_models/used-tools.model';
import { FirestoreService } from '../../shared/_services/firestore.service';
import { BasePaths } from '../../shared/_utils/base-paths.enum';

@Component({
  selector: 'app-used-tools',
  templateUrl: './used-tools.component.html',
  styleUrls: ['./used-tools.component.css']
})
export class UsedToolsComponent implements OnInit {

  private basePath = BasePaths.user_tools;

  selectedFiles: FileList;
  currentFile: File;
  currentFileUrl: any;
  percentage: number;
  
  usedToolList: UsedTools[] = [];
  usedTools: UsedTools = new UsedTools();

  constructor(
    private dbService: FirestoreService,
    private storage: AngularFireStorage
  ) { }

  ngOnInit(): void {
    this.getUsedToolsList();
  }

  selectFile(event): void {
    this.selectedFiles = event.target.files;
    this.percentage = 0;

    if (this.selectedFiles.length == 0) {
      return;
    }
    this.currentFile = this.selectedFiles.item(0);
    if (this.currentFile.type.match(/image\/*/) == null) {
        return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.currentFile); 
    reader.onload = (_event) => { 
      this.currentFileUrl = reader.result;
    }
  }

  upload(): void {
    // this.currentFile = this.selectedFiles.item(0);
    this.usedTools.toolSl = this.usedToolList.length+1;
    this.usedTools.fileName = this.currentFile.name;
    this.usedTools.filePath = new Date().getTime()+'_'+this.currentFile.name;
    // this.usedTools.toolName = this.currentFile.name; //---will be change
    this.usedTools.isActive = 1;

    const filePath = `${this.basePath}/${this.usedTools.filePath}`;
    const storageRef = this.storage.ref(filePath);
    const uploadTask = this.storage.upload(filePath, this.currentFile);
    uploadTask.snapshotChanges().pipe(
      finalize(() => {
        storageRef.getDownloadURL().subscribe(downloadURL => {
          this.usedTools.fileUrl = downloadURL;
          this.dbService.add$<any>(this.usedTools, this.basePath).then(
            res => {
              this.usedTools = new UsedTools();
              this.currentFileUrl = undefined;
            }
          )
        });
      })
    ).subscribe();
    uploadTask.percentageChanges().subscribe(
      per => {
        this.percentage = per;
      }
    );

    this.selectedFiles = undefined;
  }

  //=============================
  getUsedToolsList() {
    this.dbService.col$<UsedTools>(this.basePath).subscribe(tools => {
      this.usedToolList = tools?tools:[];
      console.log('usedToolList', this.usedToolList);
    });
  }

  deleteFileUpload(tool: UsedTools) {
    this.dbService.delete$<UsedTools>(this.basePath+'/'+tool.uid).then(res => {
      this.dbService.deleteFileStorage(tool.filePath, this.basePath);
    }, err => {
      console.log('error', err);
    })
  }



}
