import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProjectButton } from 'src/app/layouts/shared/_models/project-button.model';
import { ModalObj } from 'src/app/layouts/shared/_models/modal-obj.model';
import { FirestoreService } from 'src/app/layouts/shared/_services/firestore.service';
import { BasePaths } from 'src/app/layouts/shared/_utils/base-paths.enum';

@Component({
  selector: 'app-add-project-btn',
  templateUrl: './add-project-btn.component.html',
  styleUrls: ['./add-project-btn.component.css']
})
export class AddProjectBtnComponent implements OnInit {

  btnProject: ProjectButton = new ProjectButton();
  btnDisProjBtnSave: boolean = false;

  constructor(
    @Inject(MAT_DIALOG_DATA) public modalObj: ModalObj,
    public dialogRef: MatDialogRef<AddProjectBtnComponent>,
    private dbService: FirestoreService,
  ) { }


  ngOnInit(): void {
    if (this.modalObj.data) {
      this.btnProject = this.modalObj.data;
    }
    if(this.modalObj.type==1) {
      this.btnProject.isActive = 1;
    }
  }

  onSubmit() {
    if (this.btnProject.uid) {
      this.updateProjectActionBtn();
    } else {
      this.addProjectActionBtn();
    }
    // console.log(this.btnProject.actionType);
    
  }

  addProjectActionBtn() {
    this.btnDisProjBtnSave = true;
    this.dbService.add$<ProjectButton>(this.btnProject, BasePaths.project_details + '/' + this.btnProject.uidProj + '/actionBtnList').then(
      res => {
        this.btnDisProjBtnSave = false;
        this.modalObj.data = this.btnProject;
        this.dialogRef.close(this.modalObj);
      },
      err => {
        this.btnDisProjBtnSave = false;
      }
    )
  }
  updateProjectActionBtn() {

    this.btnDisProjBtnSave = true;
    this.dbService.updateByUid$<ProjectButton>(this.btnProject.uid, this.btnProject, BasePaths.project_details + '/' + this.btnProject.uidProj + '/actionBtnList').then(
      res => {
        this.btnDisProjBtnSave = false;
        this.modalObj.data = this.btnProject;
        this.dialogRef.close(this.modalObj);
      },
      err => {
        this.btnDisProjBtnSave = false;
      }
    )
  }

}
