import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatSidenav } from '@angular/material/sidenav';
import { map, shareReplay } from 'rxjs/operators';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { NavItem } from '../shared/_models/nav-item';
import { ConfirmObj } from '../shared/_models/confirm.model';
import { ConfirmModal } from '../shared/modals/confirm/confirm.modal';
import { AuthService } from '../security/services/auth.service';
import { ModalSetup } from '../shared/_models/modal-setup.model';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-admin-layout',
  templateUrl: './admin-layout.component.html',
  styleUrls: ['./admin-layout.component.css']
})
export class AdminLayoutComponent implements OnInit {

  @ViewChild("drawer") drawer: MatSidenav;
  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(map(result => result.matches), shareReplay());

  onClickNavItem() {
    if ('over' == this.drawer.mode) {
      this.drawer.close();
    }
  }

  showFooter: boolean = true;

  title = 'Admin Layout';
  navItems: NavItem[];

  constructor(
    private router: Router,
    private dialog: MatDialog,
    private authService: AuthService,
    private breakpointObserver: BreakpointObserver,
    // private coreResourceService: CoreResourceService,
  ) { }

  ngOnInit() {
    this.isHandset$.subscribe( res => {res?(this.showFooter=false):(this.showFooter=true)});

    this.navItems = this.allNavItems;
  }

  userProfile() {

  }
  changePassword() {

  }
  userLogout() {
    let confirmObj = new ConfirmObj(1, 'Log Out!', 'Do you want to Log Out?', 'Yes');
    const dialogRef = this.dialog.open(ConfirmModal, new ModalSetup(1, confirmObj).setup);
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.authService.removeUser();
        this.router.navigate(['login']);
      }
    });
  }

  allNavItems: NavItem[] = [
    {
      name: 'Profile Details',
      icon: 'fa fa-list',
      route: 'admin/profile-details',
    },
    {
      name: 'Project List',
      icon: 'fa fa-list',
      route: 'admin/project-list',
    },
    {
      name: 'Used Tools',
      icon: 'fa fa-list',
      route: 'admin/used-tools',
    },
    {
      name: 'Visitor Message',
      icon: 'fa fa-list',
      route: 'admin/visitor-message',
    },

    {
      name: 'Font Awesome Icons',
      icon: 'fa fa-list',
      route: 'admin/mod-faw-icon',
    },
    {
      name: 'Linear Icons',
      icon: 'ti-angle-double-right',
      route: 'admin/mod-lin-icon',
    }
  ];

}
